import React, { useState, useEffect } from "react";

export default function ForecastOverlay() {
  const [forecastData, forecastSetData] = useState(null);

  useEffect(() => {
    async function fetchData() {
      const url =
        "https://ski-resort-forecast.p.rapidapi.com/La%20Plagne/hourly?units=m&el=top&c=false";
      const options = {
        method: "GET",
        headers: {
          "X-RapidAPI-Key":
            "625e8c3452mshb467e7695aba98fp1d8b3ajsnb88b69f5e950",
          "X-RapidAPI-Host": "ski-resort-forecast.p.rapidapi.com",
        },
      };

      try {
        const response = await fetch(url, options);
        let result = await response.text();
        result = JSON.parse(result);
        console.log(result);
        if (result.forecast) {
          forecastSetData(result);
        } else {
          forecastSetData({
            forecast: [
              { time: "temp", summary: "temp", windSpeed: "temp" },
              { time: "temp", summary: "temp", windSpeed: "temp" },
              { time: "temp", summary: "temp", windSpeed: "temp" },
              { time: "temp", summary: "temp", windSpeed: "temp" },
              { time: "temp", summary: "temp", windSpeed: "temp" },
              { time: "temp", summary: "temp", windSpeed: "temp" },
              { time: "temp", summary: "temp", windSpeed: "temp" },
              { time: "temp", summary: "temp", windSpeed: "temp" },
              { time: "temp", summary: "temp", windSpeed: "temp" },
              { time: "temp", summary: "temp", windSpeed: "temp" },
              { time: "temp", summary: "temp", windSpeed: "temp" },
              { time: "temp", summary: "temp", windSpeed: "temp" },
            ],
          });
        }
      } catch (error) {
        console.error(error);
      }
    }
    fetchData();
  }, []);

  console.log(forecastData);

  return (
    <div
      style={{
        position: "absolute",
        bottom: "0",
        width: "100%",
        height: "20%",
        display: "grid",
        gridTemplateColumns: "repeat(12, 1fr)",
        justifyContent: "center",
        alignItems: "center",
        backgroundColor: "rgba(0, 0, 0, 0.5)",
        color: "white",
        pointerEvents: "auto",
        padding: "10px",
        boxSizing: "border-box",
      }}
    >
      {forecastData ? (
        <>
          {forecastData.forecast.map((forecast, index) => (
            <div
              key={index}
              style={{
                display: "flex",
                flexDirection: "column",
                justifyContent: "center",
                alignItems: "center",
              }}
            >
              <h3>{forecast.time}</h3>
              <p>{forecast.summary}</p>
              <p>{forecast.windSpeed}</p>
            </div>
          ))}
        </>
      ) : (
        <div>Loading...</div>
      )}
    </div>
  );
}
