import React, { useState, useEffect } from "react";

export default function LiftStatsOverlay() {
  const [liftStatsData, setLiftStatsData] = useState(null);

  useEffect(() => {
    async function fetchData() {
      try {
        const response = await fetch(
          "https://ski-resorts-and-conditions.p.rapidapi.com/v1/resort/courchevel",
          {
            method: "GET",
            headers: {
              "X-RapidAPI-Key":
                "625e8c3452mshb467e7695aba98fp1d8b3ajsnb88b69f5e950",
              "X-RapidAPI-Host": "ski-resorts-and-conditions.p.rapidapi.com",
            },
          }
        );
        const result = await response.json();

        if (result.message) {
          setLiftStatsData(
            "API limit reached (10/day). Please try again tomorrow."
          );
        } else {
          setLiftStatsData(result.lifts.stats);
        }
      } catch (error) {
        console.error(error);
      }
    }
    fetchData();
  }, []);

  return (
    <div
      style={{
        position: "absolute",
        top: "0",
        left: "0",
        width: "20vw",
        height: "auto",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        backgroundColor: "rgba(0, 0, 0, 0.5)",
        color: "white",
        pointerEvents: "auto",
        padding: "10px",
        boxSizing: "border-box",
      }}
    >
      <div>
        <h2>Lift Statistics</h2>
        {liftStatsData ? (
          <div>
            <h3>Open: {liftStatsData.open}</h3>
            <h3>Hold: {liftStatsData.hold}</h3>
            <h3>Scheduled: {liftStatsData.scheduled}</h3>
            <h3>Closed: {liftStatsData.closed}</h3>
            <p>{liftStatsData}</p>
          </div>
        ) : (
          <div>Loading...</div>
        )}
      </div>
    </div>
  );
}
