import React, { useState, useEffect } from "react";

export default function ResortOverlay() {
  const [resortData, setResortData] = useState(null);

  useEffect(() => {
    async function fetchData() {
      try {
        const response = await fetch(
          "https://ski-resort-forecast.p.rapidapi.com/La%20Plagne/snowConditions?units=m",
          {
            method: "GET",
            headers: {
              "X-RapidAPI-Key":
                "625e8c3452mshb467e7695aba98fp1d8b3ajsnb88b69f5e950",
              "X-RapidAPI-Host": "ski-resort-forecast.p.rapidapi.com",
            },
          }
        );

        let result = await response.text();

        result = JSON.parse(result);

        if (result.topSnowDepth) {
          setResortData(result);
        } else {
          setResortData({
            topSnowDepth: "API issue",
            botSnowDepth: "API issue",
            freshSnowfall: "API issue",
            lastSnowfallDate: "API issue",
            basicInfo: {
              name: "API issue",
              region: "API issue",
              topLiftElevation: "API issue",
              botLiftElevation: "API issue",
            },
          });
        }
      } catch (error) {
        console.error(error);
      }
    }

    fetchData();
  }, []);

  return (
    <div
      style={{
        position: "absolute",
        top: "0",
        right: "0",
        width: "20vw",
        height: "auto",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        backgroundColor: "rgba(0, 0, 0, 0.5)",
        color: "white",
        pointerEvents: "auto",
        padding: "10px",
        boxSizing: "border-box",
      }}
    >
      {resortData ? (
        <div>
          <h1>
            {resortData.basicInfo.name}, {resortData.basicInfo.region}
          </h1>
          <h2>Snow Conditions</h2>
          <h3>Top lift snow depth</h3>
          <p>{resortData.topSnowDepth}</p>
          <h3>Bottom lift snow depth</h3>
          <p>{resortData.botSnowDepth}</p>
          <h3>Fresh Snowfall</h3>
          <p>{resortData.freshSnowfall ? resortData.freshSnowfall : "0cm"}</p>
          <h3>Last Snowfall</h3>
          <p>{resortData.lastSnowfallDate}</p>
          <h3>Top lift elevation</h3>
          <p>{resortData.basicInfo.topLiftElevation}</p>
          <h3>Bottom lift elevation</h3>
          <p>{resortData.basicInfo.botLiftElevation}</p>
        </div>
      ) : (
        <div>Loading...</div>
      )}
    </div>
  );
}
