import React, { Suspense, useState, useEffect } from "react";
import { Canvas, useLoader } from "@react-three/fiber";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
import { Environment, Text, OrbitControls, Stars } from "@react-three/drei";
import * as THREE from "three";

import ResortOverlay from "./UI/ResortOverlay";
import ForecastOverlay from "./UI/ForecastOverlay";
import LiftStatsOverlay from "./UI/LiftStatsOverlay";

const runMeshes = [];

function Model() {
  const gltf = useLoader(GLTFLoader, "./assets/LaPlagne2lift.glb");

  const emptyObjects = [];
  const liftObjects = [];

  function hasAncestorWithName(object, name) {
    while (object) {
      if (object.name === name) return true;
      object = object.parent;
    }
    return false;
  }

  gltf.scene.traverse((child) => {
    if (child.isMesh) {
      if (hasAncestorWithName(child, "Topography")) {
        child.castShadow = true;
        child.receiveShadow = true;
        child.material = new THREE.MeshStandardMaterial({
          color: "white",
          side: THREE.DoubleSide,
        });
      } else if (hasAncestorWithName(child, "Buildings")) {
        child.castShadow = true;
        child.receiveShadow = true;

        // child.material = new THREE.MeshStandardMaterial({ color: "black" });
      }
    } else if (
      (child.type === "Group" || child.type === "Object3D") &&
      child.name.startsWith("Town_")
    ) {
      emptyObjects.push(child);
    }
    if (child.name.startsWith("Lift_")) {
      child.castShadow = true;
      liftObjects.push(child);
    }
    if (child.name.startsWith("Run_")) {
      runMeshes.push(child);
    }
  });

  const towns = emptyObjects.map((obj) => {
    const name = obj.name.split("_").slice(1).join(" "); // Split the name and join with a space
    return {
      name: name,
      position: obj.position,
    };
  });

  const lifts = liftObjects.map((obj) => {
    const name = obj.name.split("_").slice(1).join(" "); // Split the name and join with a space
    return {
      name: name,
      position: obj.position,
    };
  });

  return (
    <>
      <primitive object={gltf.scene} />
      {towns.map((obj) => (
        <Text
          key={obj.name}
          position={[obj.position.x, obj.position.y + 0.8, obj.position.z]}
          rotation={[Math.PI / 2, Math.PI, 0]}
          color={"black"}
          fontSize={0.4}
          outlineColor={"white"}
          fillOpacity={1}
          outlineWidth={0.03}
          font="./assets/Cuprum-VariableFont_wght.ttf"
        >
          {obj.name}
        </Text>
      ))}
      {lifts.map((obj) => (
        <Text
          key={obj.name}
          position={[obj.position.x, obj.position.y + 0.8, obj.position.z]}
          rotation={[0, Math.PI, 0]}
          color={"white"}
          fontSize={0.2}
          outlineColor={"black"}
          outlineWidth={0.03}
          font="./assets/Cuprum-VariableFont_wght.ttf"
        >
          {obj.name}
        </Text>
      ))}
    </>
  );
}

function Scene({ runMeshes }) {
  return (
    <>
      <Environment preset="sunset" />
      <Stars />
      <Model />
      <spotLight position={[30, 10, 0]} intensity={10} castShadow />
      <OrbitControls target={[0, 0, 0]} />
    </>
  );
}

function App() {
  const [runMeshesVisible, setRunMeshesVisible] = useState(true);

  const toggleRunMeshesVisibility = () => {
    setRunMeshesVisible((prevVisible) => !prevVisible);
  };

  useEffect(() => {
    runMeshes.forEach((mesh) => {
      mesh.visible = runMeshesVisible;
    });
  }, [runMeshesVisible]);

  return (
    <div style={{ width: "100vw", height: "100vh", overflow: "hidden" }}>
      <Canvas
        style={{ width: "100%", height: "100%" }}
        camera={{ position: [0, 15, -15] }}
        gl={{ antialias: true, toneMappingExposure: 0.5 }}
        shadows
      >
        <color attach="background" args={["#000000"]} />
        <Suspense fallback={null}>
          <Scene runMeshes={runMeshes} />
        </Suspense>
      </Canvas>
      <ResortOverlay />
      <ForecastOverlay />
      <LiftStatsOverlay />
      <button
        onClick={toggleRunMeshesVisibility}
        style={{ position: "absolute", top: 0, left: "50%" }}
      >
        {runMeshesVisible ? "Hide Runs" : "Show Runs"}
      </button>
    </div>
  );
}

export default App;
